import React from "react"
import { MyBase, HFormat } from "@macashipo/mlib"
import useConfigTableCell from "../../hooks/useConfigTableCell"
import styled from "styled-components"
const WrapCell = styled.div`
  display: flex;
  & > textarea {
    border: 0;
    outline: none;
    resize: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    min-height: 100%;
    height: 100%;
    text-align: right;
    background: transparent;
    &:focus {
      background: var(--color-bg-focus);
    }
  }
`
export default function MTCurrency(props) {
  const ctc = useConfigTableCell({ props })
  // console.warn("render MTCurrency:", props)
  return (
    <WrapCell className={ctc.classNameWrap("mtc-currency")}>
      <MyBase
        type="numeral"
        value={ctc.valueCell}
        onChange={v => {
          ctc.setValueCell(v)
        }}
        onBlur={v => {
          ctc.updateFieldData(v)
        }}
        attrs={{
          disabled: ctc.canEdit === true ? false : true,
        }}
        fnList={{
          getTypeInput: () => {
            return "textarea"
          },
          fnFormat: () => {
            return v => {
              return HFormat.format(v, { type: "currency" })
            }
          },
        }}
      />
    </WrapCell>
  )
}
